#youtubevideos {
    padding-left: 15%;
    padding-top: 5%;
    padding-right: 25%;
}

#youtubevideos img {
    width: 100%;
    height: auto!important;
    border-radius: 16px;
}

@media only screen and (max-width: 600px) {
    #youtubevideos {
        height: unset;
        padding-top: 30%;
        padding-left: unset;
        padding-right: unset;
    }
}